import React from 'react'
import {Grid,Rail,Segment,Header,Container,Icon,Divider,Image, Card, Modal} from 'semantic-ui-react'
import {CSSTransitionGroup} from 'react-transition-group'
import './Body.css'

export class Crowdfunding extends React.Component{
	constructor(){ super() }

	render(){
		return(
			<Modal closeIcon trigger={
				<Card link centered={this.props.phone} style={{width:this.props.phone?"80%":"29%"}}>
					<Image fluid size="large" src='https://cdn.pbrd.co/images/HKsrwfk.jpg' />
					<Card.Content>
						<Card.Header textAlign="left">Crowdfunding Site</Card.Header>
						<Card.Description>Interactive Dashboard Project</Card.Description>
					</Card.Content>
				</Card>
			}>
				<Modal.Content>
					<Modal.Description>
						<Container text textAlign="center">
						<Header textAlign="center" className="works-content-modal-bigheader"><b>Designing a crowdfunding dashboard linked with a functional database</b></Header>
						</Container>
						<Divider hidden></Divider>
						<Container text textAlign="center" style={{marginBottom:"5%"}}>
							<p className="works-content-modal-smalltext">
								The project had 2 main components to it, the implementation of a frontend dashboard, and a backend server with a database.
								Since the module was mainly focused on a database design, less attention was paid to the frontend. Nevertheless, we
								made efforts to create a frontend design that would be pleasing to the user.
							</p>
						</Container>
						<Divider clearing><b>Frontend Design</b></Divider>
						<Container text textAlign="center" style={{marginBottom:"5%", marginTop:"5%"}}>
							<p className="works-content-modal-smalltext">
								Since majority of the efforts was put into database implementation at the start, we came up with a makeshift website prototype as shown below.
								The website was built purely using HTML and CSS.
							</p>
						</Container>
						<Image fluid centered style={{width: "70%", marginTop:"3%", marginBottom:"3%"}} src='https://cdn.pbrd.co/images/HKsrCF8.png' /><br />						
						<Image fluid centered style={{width: "70%", marginBottom:"5%"}} src='https://cdn.pbrd.co/images/HKsrIR5.png' /><br />						
						<Container text textAlign="center" style={{marginBottom:"5%", marginTop:"0%"}}>
							<p className="works-content-modal-smalltext">
								After the database implementation was complete, we recreated the website with a more modern twist on its looks, focusing on a more minimalist design.
								This was implemented using ReactJS and SemanticUI. We decided to using a black/red colour scheme as the two complement each other really well.
							</p>
						</Container>
						<Image fluid centered style={{width: "70%", marginTop:"3%", marginBottom:"3%"}} src='https://cdn.pbrd.co/images/HKsrPzh.jpg' /><br />						
						<Image fluid centered style={{width: "70%", marginBottom:"5%"}} src='https://cdn.pbrd.co/images/HKsrYfc.jpg' /><br />
						<Divider clearing><b>Backend Design</b></Divider>	
						<Container text textAlign="center" style={{marginBottom:"5%", marginTop:"5%"}}>
							<Header textAlign="center" className="works-content-modal-bigheader"><b>ER diagrams</b></Header>
							<p className="works-content-modal-smalltext">
								Minimizing the number of entities in order to reduce database complexity as much as possible.
							</p>
						</Container>
						<Image floated="left" style={{width: "45%", marginTop:"2.5%",marginBottom:"2%"}} src='https://cdn.pbrd.co/images/HKss4xP.jpg' />
						<Image floated="right" style={{width: "45%", marginTop:"2.5%",marginBottom:"2%"}} src='https://cdn.pbrd.co/images/HKssaic.jpg' />	
						<Divider clearing hidden></Divider>
						<Image fluid centered style={{width: "70%", marginBottom:"5%"}} src='https://cdn.pbrd.co/images/HKssiBK.jpg' /><br />
						<Container text textAlign="center" style={{marginBottom:"7%", marginTop:"0%"}}>
							<p className="works-content-modal-smalltext">
								Following which, we built up constraints in order to make the database more robust.
								For example, because users are required to log into the website with their username, 
								each username will be unique to a user. As such, it becomes straightforward to use the 
								username as the Primary Key under the User schema.
							</p>
						</Container>
						<Container text textAlign="center" style={{marginBottom:"5%", marginTop:"5%"}}>
							<Header textAlign="center" className="works-content-modal-bigheader"><b>Other SQL Features</b></Header>
							<p className="works-content-modal-smalltext">
								Finally, we implemented features such as triggers in order to automate updates to database keys. This reduces the number
								of lines of code in our backend to update our databases, and increases the efficiency at which it operates.
							</p>
							<Header textAlign="center" className="works-content-modal-bigheader"><b>Code example of implemented trigger</b></Header>
							<Image fluid centered style={{width: "70%", marginTop:"3%", marginBottom:"3%"}} src='https://cdn.pbrd.co/images/HKsstiG.jpg' /><br />
						</Container>					
					</Modal.Description>
				</Modal.Content>
			</Modal>	
			);
	}
}